@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
* {
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    margin: 0;
}

body {
    background-color: #09001c;
}
* {
    margin: 0;
}

form {
    width: 100%;
}

.search {
    margin: 0 auto;
    margin-top: 20px;
    width: 300px;
}

input {
    height: 40px;
    text-align: center;
    color: #09001c;
    background-color: #ccc;
    border-radius: 10px;
    font-size: 20px;
    border: solid 1px #09001c;
}

input[type=text]::-webkit-input-placeholder {
    color: #09001c;
}

input[type=text]:-ms-input-placeholder {
    color: #09001c;
}

input[type=text]::placeholder {
    color: #09001c;
}

input[type=text] {
    width: 70%;
}
input[type=submit] {
    width: 30%;
}
.poke--name {
    width: 100%;
    text-align: center;
    padding: 20px 0;
}

.poke--name h2 {
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 28px;
    line-height: 33px;
    color: #ccc;
}

.poke--img {
    width:300px;
    height:300px;
    background-color: #ccc;
}

.stats--img, .stats--img > img {
    width: 40px;
    height: 40px;
    position: absolute;
    z-index: 1;
}


ul {
    z-index: 1;
    left: 0px;
    position: relative;
    list-style-type: none;
    text-decoration: none;
    display: flex;
    width: 100%;
    justify-content: center;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
}

li {
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    display: inline-block;
}

li:nth-child(1) {
    color: red;
}

li:nth-child(2) {
    color: darkslategray;
}

li:nth-child(3) {
    color: rgb(78, 3, 148);
}

.prev--poke, .next--poke {
    background: #ccc;
    border: solid 3px #09001c;
    border-radius: 10px;
    width: 145px;
    height: 50px;

    font-style: normal;
    font-weight: bold;
    font-size: 28px;
    line-height: 33px;
}

.poke--control {
    display: flex;
    width: 300px;
    margin: 10px auto;
    justify-content: space-between;
}

